.header{
border: 2px solid red;
height: 65px;
display: flex;
background-color: rgb(211, 7, 147);
color: white;
}
.logo{
width: 20%;
}
.menus{
width: 80%;
display: flex;
justify-content: space-evenly;
align-items: center;

}
.link{
    color: white;
    text-decoration: none;
    font-size: 25px;
    font-weight: bolder;
}
.logo-img{
    width: 100px;
    margin-left: 25px;
    margin-top: 6px;
    display: inline-block;
    
}
.lable{
    display: inline-block;
    width: 20%;
}
.space{
    margin-bottom: 15px;
}
.customers {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  .customers td, .customers th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  .customers tr:nth-child(even){background-color: #f2f2f2;}
  
  .customers tr:hover {background-color: #ddd;}
  
  .customers th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #60dd0c;
    color: white;
  }
  
   .circle-image{ 
    border-radius: 70px;
    width: 100px;
    height: 100px;
  }